import { useHistory, useParams } from 'react-router-dom';

import logoImg from '../assets/images/logo.svg';
import deleteImg from '../assets/images/delete.svg';
import checkImg from '../assets/images/check.svg';
import answerImg from '../assets/images/answer.svg';

import { Button } from '../components/Button';
import { RoomCode } from '../components/RoomCode';

import { Question } from '../components/Question/index';
import { useRoom } from '../hooks/useRoom';

import '../styles/room.scss';
import { database } from '../services/firebase';


type RoomParams = {
    id: string;
}

export function AdminRoom() {
    //const { user } = useAuth();
    const history = useHistory()
    const params = useParams<RoomParams>();
    const roomId =  params.id;

    const { title, questions} = useRoom(roomId)

    async function handleEndRoom() {
        await database.ref(`rooms/${roomId}`).update({
            endedAt: new Date(),
        })

        history.push('/');
    }

    async function handleDeleteQuestion(questionId: string) {
      if  (window.confirm('Você tem certeza que deseja excluir esta pergunta?')) {
        await database.ref(`rooms/${roomId}/questions/${questionId}`).remove();
      }
    }

    async function handleCheckQuestionAsAnswerd(questionId: string) {
        await database.ref(`rooms/${roomId}/questions/${questionId}`).update({
            isAnswerd: true,
        })
    }

    async function handleHightLightQuestion(questionId: string) {
        await database.ref(`rooms/${roomId}/questions/${questionId}`).update({
            isHightLighted: true,
        })
    }


    return(
        <div id="page-room">
          <header>
            <div className="content">
              <img src={logoImg} alt="Letmeask" />
              <div>
                <RoomCode code={roomId} />
                <Button isOutlined onClick={handleEndRoom}>Encerrar sala</Button>
              </div>
            </div>
          </header>

            <main>
                <div className="room-title">
                    <h1>Sala {title}</h1>
                    { questions.length > 0 && <span>{questions.length} pergunta(s)</span>}
                </div>

                <div className="question-list">
                {questions.map(question => {
                    return (
                        <Question
                            key={question.id}
                            content={question.content}
                            author={question.author}
                            isAnswerd={question.isAnswerd}
                            isHightLighted={question.isHightLighted}
                        >
                            {!question.isAnswerd && (
                    <>
                    <button
                        type="button"
                        onClick={() => handleCheckQuestionAsAnswerd(question.id)}
                    >
                    <img src={checkImg} alt="Marcar pergunta como respondida" />
                    </button>
                            
                     <button
                        type="button"
                        onClick={() => handleHightLightQuestion(question.id)}
                    >
                    <img src={answerImg} alt="dar destaque para a pergunta" />
                    </button>
                            </>
                            )}

                            <button
                                type="button"
                                onClick={() => handleDeleteQuestion(question.id)}
                            >
                                <img src={deleteImg} alt="Remover pergunta" />
                            </button>
                        </Question>
                    );
                })}             
                </div>
            </main>
        </div>
    );
}